.app__header{
 display: flex;
 height: 60px;
 align-items: center;
 background-color: #131921;
 position: sticky;
 top: 0;
 z-index: 100;
 width: max-content;
 width: 100%;
}
.app__logo{
    width: 100px;
    object-fit: contain;
    margin: 0 20px;
    margin-top: 18px;
}
.header__searchicon{
    padding: 5px;
    height: 22px !important;
    background-color: #cd9042;
}
.header__search{
    display: flex;
    flex: 1;
    align-items: center; 
    border-radius: 24px;

}
.header__searchinput{
    height: 10px;
    padding: 12px;
    border: none;
    width: 100%;
}
.header__option{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
    color: white;
    text-decoration: none;
}
.header__navbar{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
}
.header__optionone{
    font-size: 10px;
    text-decoration: unset;
}
.header__optiontwo{
    font-size: 13px;
    font-weight: 800;
    text-decoration: none;
}
.header__openBasket{
    display: flex;
    align-items: center;
    color: white;
}
.header__BasketCount{
    margin-left: 10px;
    margin-right: 10px;
}

