 .home{
    display: flex;
    justify-content: center; 
    margin-left: auto;
    margin-right: auto; 
    max-width: 1500px;
    width: 100%;
}   
.product__row{
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
    /* width: 95%;  */
}

@media(max-width:500px){
    .home{
        display: flex;
        justify-content: center;  
        margin-left: auto;
        margin-right: auto; 
        max-width: 600;
        width: 100%;
    }   
    .product__row{
        display: flex;
        flex-direction: column;
        z-index: 1;
        margin-left: 5px;
        margin-right: 5px;
        justify-content: center;
        max-width: 600px;
        width: 85%;
    }
}


.product{ 
    display: flex; 
    flex-direction: column;  
    align-items: center; 
    justify-content: center; 
    margin: 10px; 
    padding: 20px;
    width: 100%;
    max-height: 400px;   
    min-width: 100px;
    background-color:white;
    z-index: 1 ;
 }   
 
 
.product__image{
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}
.product__price{
    margin-top: 5px;
}
.product__info{
    margin-bottom: 5px;
}
.button{
    background: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
}
.product__rating{
    display: flex;
}

.checked{
    color: orange;
}
.checkout__ad{
    width: 100%;
    margin-bottom: 10px;
    height: 25vh;
}
.checkout{
    display: flex;
    background-color: white;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content; 
    padding: 20px; 
}
.subtotal{
    display: flex;
    flex-direction: column;
    justify-content: space-between; 
    padding: 20px;
    width: 300px;
    height: 100px;
    border: 1px solid #dddddd;
    background-color: #f3f3f3;
    border-radius: 3px;
}
.subtotal__gift{
    display: flex;
    align-items: center;
}

.subtotal__gift>input{
    margin-right: 5px;
}
.subtotal>button{
    border-color: #a88734 #9c7e31 #846a29;
    background: #f0c14b;
    width: 100%;
    height: 30px;
    border-radius: 2px;
    border: 1px solid;
    color: #111;
    margin-top: 10px;
}
.checkoutProduct{
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
}
.checkoutProduct__info{
    padding-left: 20px;
    /* width: 200px; */
}
.checkoutProduct__info>button{
    background: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
}
.checkoutProduct__image{
    object-fit: contain;
    width: 180px;
    height: 180px;
}
.checkoutProduct__rating{
    display: flex;
}
.checkoutProduct__title{
    font-size: 17px;
    font-weight: 800;
}
.login{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    background-color:white ;
}
.login__image{
    margin-top: 20px;
    margin-bottom: 20px;
    margin-left: auto;
    margin-right: auto;
    object-fit: contain;
    width: 100px;
}
.login__container{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 300px;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid lightgrey;
    padding: 20px;
}
.login__container>h1{
    font-weight: 500;
    margin-bottom: 20px;
}
.login__container>form>h5{
    margin-bottom: 5px;
}
.login__container>form>input{
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width: 98%;

}
.login__container>p{
    margin-top: 15px;
    font-size: 12px;
}
.login__signIn{
    background: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: darkgrey;
    color: #342e20;
    width: 100%;
    height: 30px;

}
.login__register{
    background: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: darkgrey;
    width: 100%;
    height: 30px;
}
.payment{
    background-color: white;
}
.payment__container>h1{
    text-align: center;
    padding: 10px;
    font-weight: 400;
    background-color: rgb(234,237,237);
    border-bottom:1px solid grey;
}
/* .payment__container>h1 a{
    text-decoration: none;
} */
.payment__section{
    display: flex;
    margin: 0 20px;
    padding: 20px;
    border-bottom: 1px solid lightgrey;
}
.payment__title{
    flex: 0.2 1;
}
.payment__address, .payment__items,.payment__details{
    flex: 0.8 1;
}

.payment__details>form{
    max-width: 400px;
}
.payment__details>h3{
    padding-bottom: 20px;
}

.payment__details>form>div>button{
    background: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    font-weight: bolder;
    margin-top: 10px;
    color: #111;
    border-color: #a88734 #9c7e31 #846a29;
}
.orders{
    padding: 20px 80px;
}

.orders>h1{
    margin: 30px 0;
}
.order{
    padding: 40px;
    margin: 20px 0;
    border: 1px solid lightgrey;
    background-color: white;
    position: relative;
}

.order__id{
    position: absolute;
    top: 40px;
    right: 20px;
}

.order__total{
    font-weight: 500;
    text-align: right;
}
