.login{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    background-color:white ;
}
.login__image{
    margin-top: 20px;
    margin-bottom: 20px;
    margin-left: auto;
    margin-right: auto;
    object-fit: contain;
    width: 100px;
}
.login__container{
    height: fit-content;
    width: 300px;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid lightgrey;
    padding: 20px;
}
.login__container>h1{
    font-weight: 500;
    margin-bottom: 20px;
}
.login__container>form>h5{
    margin-bottom: 5px;
}
.login__container>form>input{
    height: 30px;
    margin-bottom: 10px;
    background-color: white;
    width: 98%;

}
.login__container>p{
    margin-top: 15px;
    font-size: 12px;
}
.login__signIn{
    background: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: darkgrey;
    color: #342e20;
    width: 100%;
    height: 30px;

}
.login__register{
    background: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: darkgrey;
    width: 100%;
    height: 30px;
}